import { Component, OnInit, Input, Output , EventEmitter } from '@angular/core';

@Component({
  selector: 'app-botao-dropdown',
  templateUrl: './botao-dropdown.component.html',
  styleUrls: ['./botao-dropdown.component.css']
})
export class BotaoDropdownComponent implements OnInit {


  @Input() lista:string[];
  @Input() nome:string;
  @Input() classeBotao:any = null;
  @Input() classeOpcoes:any = null;
  @Input() habilitarBotaoRemover:boolean = false;
  @Output() itemClicado =  new EventEmitter;
  @Output() removerClicado =  new EventEmitter;

  classeFechar:any={'botaoSair':true};

  constructor() { }

  ngOnInit() {
  }

  clickItem(item:string):void{
    this.itemClicado.emit(item);
  }

  clickRemover(item:string):void{
    this.removerClicado.emit(item);
  }

  classes():any{
    if(this.classeBotao != null){
        return this.classeBotao;
    }else{
      return {'btn-primary':true}};
  }

  teste(texto:string):void{
    console.log(texto);
  }

  listaVazia():boolean{
    if(this.lista.length==0){
      return true;
    }else{
      return false;
    }
  }

}



